.modal-style {
  display:flex;
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #3b3b3bd0;
  width: 700px;
  border: 3px solid #000000ad;
  flex-direction: column;
  padding: 20px;
  z-index: 5;
}

.modal-style-2 {
  background-color: #4d4d4deb;
}

.mytbl{
  max-height: calc(100vh - 120px);
  overflow: scroll;
}


@media screen and (max-width: 980px) {
}
@media screen and (max-width: 768px) {
  .modal-style {
    width: 600px;
  }
}
@media screen and (max-width: 575px) {
  .modal-style {
    width: 300px;
  }
}